import React, { Component } from 'react';
import { randPercent, integer, rand } from '../../../../utils';

class ColorCellRenderer extends Component {
  render() {
    let random = rand(0, 1);
    let style = {};
    if (random > 0.5) {
      style = { color: '#FF5C5C' };
    } else {
      style = { color: '#39E365' };
    }
    return <span style={style}>{this.props.data[this.props.colDef.field]}</span>;
  }
}

export default ColorCellRenderer;



