/* eslint-disable */
import { randPercent, integer, rand } from "../utils";
import cellRenderer from "../routes/Wealth/components/ColorCellRenderer/ColorCellRenderer";
import Faker from "faker-zh-cn";
import _ from 'lodash';

export default {
  // model名称
  namespace: "wealth",
  // state
  state: {
    hangQingRowData: [],
    hangQingColumnData: [],
    zengCangRowData: [],
    zengCangColumn: [],
    ziJinRowData: [],
    ziJinColumn: [],
    DDEColumn: [],
    DDERowData: [],
    yingLiColumn: [],
    yingLiRowData: []
  },
  // 路由监听
  subscriptions: {
    setup({ dispatch, history }) {
      history.listen(location => {});
    }
  },
  // generator 函数
  effects: {
    *init({ payload }, { call, put }) {
      yield put({
        type: "updateState"
      });
    }
  },
  // reducer
  reducers: {
    // 更新state
    updateState(state, action) {
      return { ...state, ...action.payload };
    },
    getHangQingData(state, action) {
      function generateRowData() {
        let retData = [];
        for (let index = 0; index < 2000; index++) {
          retData.push({
            id: index + 1,
            code: integer(100000, 999999),
            name: Faker.Company.companyName(),
            latest: rand(1, 50),
            increase: rand(10, 15),
            decrease: rand(10, 15),
            zong_shou: integer(5000, 100000),
            xian_shou: integer(1, 20),
            mai_ru_jia: integer(10, 100),
            mai_chu_jia: integer(10, 100),
            zhang_su_percent: rand(0.02, 0.05),
            huan_shou_percent: rand(2, 10),
            value: `${integer(1000, 9000)}万`,
            shi_ying_lv: integer(20, 100),
            suo_shu_hang_ye: Faker.Company.companyName(),
            max: rand(10, 50),
            mini: rand(10, 50),
            kai_pan: rand(10, 100),
            zuo_shou: rand(10, 100),
            zhen_fu_percent: rand(10, 100),
            liang_bi: rand(2, 10),
            wei_bi_percent: rand(10, 100),
            jun_jia: rand(10, 100),
            nei_pan: rand(10, 100),
            wai_pan: integer(10, 100),
            nei_wai_bi: rand(0.05, 2),
            mai_yi_liang_buy: integer(10, 100),
            mai_yi_liang_sold: integer(10, 100),
            shi_jing_lv: integer(10, 100),
            zong_gu_ben: integer(5000, 100000),
            zong_shi_zhi: `${integer(30, 100)}亿`,
            liu_tong_gu_ben: `${integer(3, 20)}亿`,
            liu_tong_shi_zhi: `${integer(3, 20)}亿`,
            zhang_fu_3: rand(2, 10),
            zhang_fu_6: rand(2, 10),
            huan_shou_3: rand(2, 10),
            huan_shou_6: rand(2, 10)
          });
        }
        return retData;
      }
      const columnDefs = [
        { headerName: "序", field: "id", width: 80 },
        { headerName: "代码", field: "code", width: 80 },
        { headerName: "名称", field: "name", width: 80 },
        {
          headerName: "*",
          field: "icon",
          width: 80
        },
        {
          headerName: "最新",
          field: "latest",
          width: 80,
          cellRendererFramework: cellRenderer
        },
        {
          headerName: "涨幅%",
          field: "increase",
          width: 80,
          cellRendererFramework: cellRenderer
        },
        {
          headerName: "涨跌",
          field: "decrease",
          width: 80
        },
        {
          headerName: "总手",
          field: "zong_shou",
          width: 80
        },
        {
          headerName: "现手",
          field: "xian_shou",
          width: 80
        },
        {
          headerName: "买入价",
          field: "mai_ru_jia",
          width: 80,
          cellRendererFramework: cellRenderer
        },
        {
          headerName: "卖出价",
          field: "mai_chu_jia",
          width: 80
        },
        {
          headerName: "涨速%",
          field: "zhang_su_percent",
          width: 80
        },
        {
          headerName: "换手%",
          field: "huan_shou_percent",
          width: 80
        },
        {
          headerName: "金额",
          field: "value",
          width: 80
        },
        {
          headerName: "市盈率",
          field: "shi_ying_lv",
          width: 80,
          cellRendererFramework: cellRenderer
        },
        { headerName: "所属行业", field: "suo_shu_hang_ye", width: 80 },
        {
          headerName: "最高",
          field: "max",
          width: 80
        },
        {
          headerName: "最低",
          field: "mini",
          width: 80
        },
        {
          headerName: "开盘",
          field: "kai_pan",
          width: 80
        },
        {
          headerName: "昨收",
          field: "zuo_shou",
          width: 80
        },
        {
          headerName: "振幅%",
          field: "zhen_fu_percent",
          width: 80
        },
        {
          headerName: "量比",
          field: "liang_bi",
          width: 80
        },
        {
          headerName: "委比%",
          field: "wei_bi_percent",
          width: 80
        },
        {
          headerName: "委差",
          field: "wei_cha",
          width: 80
        },
        {
          headerName: "均价",
          field: "jun_jia",
          width: 80
        },
        {
          headerName: "内盘",
          field: "nei_pan",
          width: 80
        },
        {
          headerName: "外盘",
          field: "wai_pan",
          width: 80
        },
        {
          headerName: "内外比",
          field: "nei_wai_bi",
          width: 80
        },
        {
          headerName: "买一量",
          field: "mai_yi_liang_buy",
          width: 80
        },
        {
          headerName: "卖一量",
          field: "mai_yi_liang_sold",
          width: 80
        },
        {
          headerName: "市净率",
          field: "shi_jing_lv",
          width: 80
        },
        {
          headerName: "总股本",
          field: "zong_gu_ben",
          width: 80
        },
        {
          headerName: "总市值",
          field: "zong_shi_zhi",
          width: 80
        },
        {
          headerName: "流通股本",
          field: "liu_tong_gu_ben",
          width: 80
        },
        {
          headerName: "流通市值",
          field: "liu_tong_shi_zhi",
          width: 80
        },
        {
          headerName: "3日涨幅%",
          field: "zhang_fu_3",
          width: 80
        },
        {
          headerName: "6日涨幅%",
          field: "zhang_fu_6",
          width: 80
        },
        {
          headerName: "3日换手%",
          field: "huan_shou_3",
          width: 80
        },
        {
          headerName: "6日换手%",
          field: "huan_shou_6",
          width: 80
        }
      ];
      const rowData = generateRowData();

      return {
        ...state,
        hangQingColumnData: columnDefs,
        hangQingRowData: rowData
      };
    },
    getZengCangData(state, action) {
      let groupColumn = [
        { headerName: '今日增仓排名', field: 'zeng_cang_today', width: 330, children: [] },
        { headerName: '3日增仓排名', field: 'zeng_cang_3', width: 330, children: [] },
        { headerName: '5日增仓排名', field: 'zeng_cang_5', width: 330, children: [] },
        { headerName: '10日增仓排名', field: 'zeng_cang_10', width: 330, children: [] }
      ];
      let childColumn = [
        { headerName: '增仓占比', field: 'zeng_cang_zhan_bi', width: 80 },
        { headerName: '排名', field: 'pai_ming', width: 80 },
        { headerName: '排名变化', field: 'pai_ming_bian_hua', width: 80 },
        { headerName: '涨幅%', field: 'zhang_fu_percent', width: 80 }
      ];

      function generateRowData() {
        let groupColumnClone = _.cloneDeep(groupColumn);
        let childColumnClone = _.cloneDeep(childColumn);
        let retData = [];
        for (let index = 0; index < 2000; index++) {
          let rowItem = {
            id: index + 1,
            code: integer(100000, 999999),
            name: Faker.Company.companyName(),
            latest: rand(1, 50),
            increase: rand(10, 15),
            suo_shu_hang_ye: Faker.Company.companyName()
          };
          for (let k = 0; k < groupColumnClone.length; k++) {
            const parentColumn = groupColumnClone[k];
            const { headerName: headerNameParent, field: fieldParent } = parentColumn;
            for (let j = 0; j < childColumnClone.length; j++) {
              const child = childColumnClone[j];
              const { headerName, field } = child;
              let randomData = '';
              if (field == 'zeng_cang_zhan_bi') {
                randomData = rand(10, 100);
              } else if (field == 'pai_ming') {
                randomData = index + 1;
              } else if (field == 'pai_ming_bian_hua') {
                randomData = `+${integer(1,1000)}`;
              } else if (field == 'zhang_fu_percent') {
                randomData = rand(10, 100);
              }
              rowItem[`${fieldParent}${field}`] = randomData
            }
          }
          retData.push(rowItem)
        }
        return retData;
      };
      function generateColumnDefs(){
        let columnDefs = [
          { headerName: '序', field: 'id', width: 80 },
          { headerName: '代码', field: 'code', width: 80 },
          { headerName: '名称', field: 'name', width: 80 },
          { headerName: '*', field: 'icon', width: 80, cellRendererFramework: cellRenderer },
          { headerName: '最新', field: 'latest', width: 80, cellRendererFramework: cellRenderer },
          { headerName: '涨幅%', field: 'increase', width: 80, cellRendererFramework: cellRenderer },
          { headerName: '所属行业', field: 'suo_shu_hang_ye', width: 80 }
        ];
        let groupColumnClone = _.cloneDeep(groupColumn);
        let childColumnClone = _.cloneDeep(childColumn);
        for (let index = 0; index < groupColumnClone.length; index++) {
          const parentColumn = groupColumnClone[index];
          const { headerName: headerNameParent, field: fieldParent } = parentColumn;
          for (let j = 0; j < childColumnClone.length; j++) {
            const child = childColumnClone[j];
            const { headerName, field } = child;
            parentColumn.children.push({
              headerName: headerName,
              field: fieldParent + field
            });
          }
        }
        return columnDefs.concat(groupColumnClone);
      };
      const columnDefs = generateColumnDefs();
      const rowData = generateRowData();
      return { ...state, ...action.payload, zengCangRowData:  rowData, zengCangColumn: columnDefs};
    },
    getZiJinData(state, action) {
      let groupColumn = [
        { headerName: '实时超大单统计', field: 'chao_da_count', width: 330, children: [] },
        { headerName: '实时大单统计', field: 'da_count', width: 330, children: [] },
        { headerName: '实时中单统计', field: 'zhong_count', width: 330, children: [] },
        { headerName: '实时小单统计', field: 'xiao_count', width: 330, children: [] }
      ];
      let childColumn = [
        { headerName: '流入', field: 'liu_ru', width: 80 },
        { headerName: '流出', field: 'liu_chu', width: 80 },
        { headerName: '净额', field: 'jing_e', width: 80 },
        { headerName: '净占比%', field: 'jing_zhan_percent', width: 80 }
      ];
      function generateRowData(){
        let groupColumnClone = _.cloneDeep(groupColumn);
        let childColumnClone = _.cloneDeep(childColumn);
        let retData = [];
        for (let index = 0; index < 2000; index++) {
          let rowItem = {
            id: index + 1,
            code: integer(100000, 999999),
            name: Faker.Company.companyName(),
            latest: rand(1, 50),
            increase: rand(10, 15),
            zhu_li_liu_ru: `${rand(1, 3)}亿`,
            ji_he_jing_jia: integer(100, 2000)
          };
          for (let k = 0; k < groupColumnClone.length; k++) {
            const parentColumn = groupColumnClone[k];
            const { headerName: headerNameParent, field: fieldParent } = parentColumn;
            for (let j = 0; j < childColumnClone.length; j++) {
              const child = childColumnClone[j];
              const { headerName, field } = child;
              let randomData = '';
              if (field == 'liu_ru') {
                randomData = `${rand(10, 100)}亿`;
              } else if (field == 'liu_chu') {
                randomData = `${rand(10, 100)}亿`;
              } else if (field == 'jing_e') {
                randomData = `${rand(10, 100)}亿`;
              } else if (field == 'jing_zhan_percent') {
                randomData = rand(10, 100);
              }
              rowItem[`${fieldParent}${field}`] = randomData;
            }
          }
          retData.push(rowItem);
        }
        return retData;
      };
      function generateColumnDefs(){
        let columnDefs = [
          { headerName: '序', field: 'id', width: 80, pinned: 'left' },
          { headerName: '代码', field: 'code', width: 80, pinned: 'left' },
          { headerName: '名称', field: 'name', width: 80, pinned: 'left' },
          { headerName: '最新', field: 'latest', width: 80, cellRendererFramework: cellRenderer },
          { headerName: '涨幅%', field: 'increase', width: 80, cellRendererFramework: cellRenderer },
          { headerName: '主力净流入', field: 'zhu_li_liu_ru', width: 80 },
          { headerName: '集合竞价', field: 'ji_he_jing_jia', width: 80 }
        ];
        let groupColumnClone = _.cloneDeep(groupColumn);
        let childColumnClone = _.cloneDeep(childColumn);
        for (let index = 0; index < groupColumnClone.length; index++) {
          const parentColumn = groupColumnClone[index];
          const { headerName: headerNameParent, field: fieldParent } = parentColumn;
          for (let j = 0; j < childColumnClone.length; j++) {
            const child = childColumnClone[j];
            const { headerName, field } = child;
            parentColumn.children.push({
              headerName: headerName,
              field: fieldParent + field
            });
          }
        }
        return columnDefs.concat(groupColumnClone);
      };
      const columnDefs = generateColumnDefs();
      const rowData = generateRowData();
      return { ...state, ...action.payload, ziJinRowData: rowData, ziJinColumn: columnDefs };
    },
    getDDEData(state, action) {
      function generateRowData(){
        let retData = [];
        for (let index = 0; index < 2000; index++) {
          let rowItem = {
            id: index + 1,
            code: integer(100000, 999999),
            name: Faker.Company.companyName(),
            latest: rand(1, 50),
            increase: rand(10, 15),
            DDX_today: rand(1,10),
            DDY_today: rand(1,10),
            DDZ_today: rand(1,10),
            DDX_5: rand(1,10),
            DDY_5: rand(1,10),
            DDX_10: rand(1,10),
            DDY_10: rand(1,10),
            DDX_RED_con: `${integer(2,10)}天`,
            DDX_RED_5: `${integer(2,10)}天`,
            DDX_RED_10: `${integer(2,10)}天`,
            te_da_buy: rand(10,30),
            te_da_sold: rand(10,30),
            te_da_percent: rand(10,30),
            da_dan_buy: rand(10,30),
            da_dan_sold: rand(10,30),
            da_dan_percent: rand(10,30),
          };
    
          retData.push(rowItem);
        }
        return retData;
      };
      function generateColumnDefs(){
        let columnDefs = [
          { headerName: '序', field: 'id', width: 80, pinned: 'left' },
          { headerName: '代码', field: 'code', width: 80, pinned: 'left' },
          { headerName: '名称', field: 'name', width: 80, pinned: 'left' },
          { headerName: '*', field: 'icon', width: 80, cellRendererFramework: cellRenderer, pinned: 'left' },
          { headerName: '最新', field: 'latest', width: 80, cellRendererFramework: cellRenderer },
          { headerName: '涨幅%', field: 'increase', width: 80, cellRendererFramework: cellRenderer },
    
          {
            headerName: '当日资金流',
            field: 'zi_jin_today',
            width: 80,
            cellRendererFramework: cellRenderer,
            children: [
              { headerName: 'DDX', field: 'DDX_today', width: 80, cellRendererFramework: cellRenderer },
              { headerName: 'DDY', field: 'DDY_today', width: 80, cellRendererFramework: cellRenderer },
              { headerName: 'DDZ', field: 'DDZ_today', width: 80, cellRendererFramework: cellRenderer }
            ]
          },
          {
            headerName: '5日资金流',
            field: 'zi_jin_5',
            width: 80,
            cellRendererFramework: cellRenderer,
            children: [
              { headerName: '5日DDX', field: 'DDX_5', width: 80, cellRendererFramework: cellRenderer },
              { headerName: '5日DDY', field: 'DDY_5', width: 80, cellRendererFramework: cellRenderer }
            ]
          },
          {
            headerName: '10日资金流',
            field: 'zi_jin_10',
            width: 80,
            cellRendererFramework: cellRenderer,
            children: [
              { headerName: '10日DDX', field: 'DDX_10', width: 80, cellRendererFramework: cellRenderer },
              { headerName: '10日DDY', field: 'DDY_10', width: 80, cellRendererFramework: cellRenderer }
            ]
          },
          {
            headerName: 'DDX飘红天数',
            field: 'DDX_RED',
            width: 80,
            cellRendererFramework: cellRenderer,
            children: [
              { headerName: '连续', field: 'DDX_RED_con', width: 80, cellRendererFramework: cellRenderer },
              { headerName: '5日内', field: 'DDX_RED_5', width: 80, cellRendererFramework: cellRenderer },
              { headerName: '10日内', field: 'DDX_RED_10', width: 80, cellRendererFramework: cellRenderer }
            ]
          },
          { headerName: '特大买入%', field: 'te_da_buy', width: 80, cellRendererFramework: cellRenderer },
          { headerName: '特大卖出%', field: 'te_da_sold', width: 80, cellRendererFramework: cellRenderer },
          { headerName: '特大单净比%', field: 'te_da_percent', width: 80, cellRendererFramework: cellRenderer },
          { headerName: '大单买入%', field: 'da_dan_buy', width: 80, cellRendererFramework: cellRenderer },
          { headerName: '大单卖出%', field: 'da_dan_sold', width: 80, cellRendererFramework: cellRenderer },
          { headerName: '大单净比%', field: 'da_dan_percent', width: 80, cellRendererFramework: cellRenderer }
        ];
        return columnDefs
      };
      const columnDefs = generateColumnDefs();
      const rowData = generateRowData();
      return { ...state, ...action.payload, DDERowData: rowData, DDEColumn: columnDefs };
    },
    getYingLiData(state, action) {
      function generateRowData(){
        let retData = [];
        for (let index = 0; index < 2000; index++) {
          let rowItem = {
            id: index + 1,
            code: integer(100000, 999999),
            name: Faker.Company.companyName(),
            latest: rand(1, 50),
            yan_bao: '研报',
            hang_qing: '行情',
            latest: rand(1, 50),
            zhang_fu: rand(1, 50),
            yan_bao_shu: integer(1, 50),
            mai_ru: rand(1, 50),
            zeng_chi: rand(1, 50),
            zhong_xing: rand(1, 50),
            jian_chi: rand(1, 50),
            mai_chu: rand(1, 50),
            shou_yi: rand(1, 50),
            shou_yi_2017: rand(1, 50),
            shi_ying_lv_2017: rand(1, 50),
            shou_yi_2018: rand(1, 50),
            shi_ying_lv_2018: rand(1, 50),
            shou_yi_2019: rand(1, 50),
            shi_ying_lv_2019: rand(1, 50),
            shou_yi_2020: rand(1, 50),
            shi_ying_lv_2020: rand(1, 50)
          };
    
          retData.push(rowItem);
        }
        return retData;
      };
      function generateColumnDefs(){
        let columnDefs = [
          { headerName: '序', field: 'id', width: 80, pinned: 'left' },
          { headerName: '代码', field: 'code', width: 80, pinned: 'left' },
          { headerName: '名称', field: 'name', width: 80, pinned: 'left' },
          {
            headerName: '相关',
            field: 'relative',
            width: 160,
            children: [
              { headerName: '研报', field: 'yan_bao', width: 80 },
              { headerName: '行情', field: 'hang_qing', width: 80 }
            ]
          },
          { headerName: '最新', field: 'latest', width: 80, cellRendererFramework: cellRenderer },
          { headerName: '涨幅%', field: 'zhang_fu', width: 80, cellRendererFramework: cellRenderer },
          { headerName: '研报数', field: 'yan_bao_shu', width: 80, cellRendererFramework: cellRenderer },
          {
            headerName: '机构投资评级家次（近六个月）',
            field: 'tou_zi_ping_ji_6',
            width: 80,
            children: [
              { headerName: '买入', field: 'mai_ru', width: 80, cellRendererFramework: cellRenderer },
              { headerName: '增持', field: 'zeng_chi', width: 80, cellRendererFramework: cellRenderer },
              { headerName: '中性', field: 'zhong_xing', width: 80, cellRendererFramework: cellRenderer },
              { headerName: '减持', field: 'jian_chi', width: 80, cellRendererFramework: cellRenderer },
              { headerName: '卖出', field: 'mai_chu', width: 80, cellRendererFramework: cellRenderer }
            ]
          },
          {
            headerName: '2016实际',
            field: 'shi_ji_2016',
            width: 80,
            children: [{ headerName: '收益', field: 'shou_yi', width: 80, cellRendererFramework: cellRenderer }]
          },
          {
            headerName: '2017预测',
            field: 'yu_ce_2017',
            width: 80,
            cellRendererFramework: cellRenderer,
            children: [
              { headerName: '收益', field: 'shou_yi_2017', width: 80, cellRendererFramework: cellRenderer },
              { headerName: '市盈率', field: 'shi_ying_lv_2017', width: 80, cellRendererFramework: cellRenderer }
            ]
          },
          {
            headerName: '2018预测',
            field: 'yu_ce_2018',
            width: 80,
            cellRendererFramework: cellRenderer,
            children: [
              { headerName: '收益', field: 'shou_yi_2018', width: 80, cellRendererFramework: cellRenderer },
              { headerName: '市盈率', field: 'shi_ying_lv_2018', width: 80, cellRendererFramework: cellRenderer }
            ]
          },
          {
            headerName: '2019预测',
            field: 'yu_ce_2019',
            width: 80,
            cellRendererFramework: cellRenderer,
            children: [
              { headerName: '收益', field: 'shou_yi_2019', width: 80, cellRendererFramework: cellRenderer },
              { headerName: '市盈率', field: 'shi_ying_lv_2019', width: 80, cellRendererFramework: cellRenderer }
            ]
          },
          {
            headerName: '2020预测',
            field: 'yu_ce_2020',
            width: 80,
            cellRendererFramework: cellRenderer,
            children: [
              { headerName: '收益', field: 'shou_yi_2020', width: 80, cellRendererFramework: cellRenderer },
              { headerName: '市盈率', field: 'shi_ying_lv_2020', width: 80, cellRendererFramework: cellRenderer }
            ]
          }
        ];
        return columnDefs;
      };
      const columnDefs = generateColumnDefs();
      const rowData = generateRowData();

      return { ...state, ...action.payload, yingLiRowData: rowData, yingLiColumn: columnDefs };
    },
  }
};



